a {
	font-weight: 		$link-font-weight;
	text-decoration: 	$link-text-decoration;
	cursor: pointer;
	
	&:link {
		@include toggleDarkMode(color, $link-color, $link-color-dark);
	}
	&:visited {
		@include toggleDarkMode(color, $link-color, $link-color-dark);
	}
	&:hover {
		@include toggleDarkMode(color, $link-color--hover, $link-color--hover-dark);
		text-decoration: 	$link-text-decoration-hover;	
	}
	&:focus {
		@include toggleDarkMode(color, $link-color--focus, $link-color--focus-dark);
		@include toggleDarkMode(background, $link-background--focus, $link-background--focus-dark);
		@include toggleDarkMode(outline, $link-outline--focus, $link-outline--focus-dark);
	}
	&:active {
		text-decoration: 	$link-text-decoration-active;
	}
}

p,
dt,
dd,
li {
	
	@include toggleSizes(font-size, $font-size--p--xs, $font-size--p--sm, $font-size--p--md, $font-size--p--lg, $font-size--p--xl);
	@include toggleSizes(line-height, $line-height--p--xs, $line-height--p--sm, $line-height--p--md, $line-height--p--lg, $line-height--p--xl);
	@include toggleSizes(padding, $padding--p--xs, $padding--p--sm, $padding--p--md, $padding--p--lg, $padding--p--xl);
	
	font-weight:		normal;
	
	&.lead {
		padding-bottom: 	0;			
		font-weight:		bold;
	}

	span.small-caps {
		font-variant: 	small-caps !important;
		font-size: 		120%;
	}
	
}

li {
	padding-bottom: 0;
}

ul {
	
	list-style: none;
	padding-top: 0;
	@include toggleSizes(padding-left, ($font-size--p--xs * 1.5), ($font-size--p--sm * 1.5), 0, 0, 0);
	@include toggleSizes(padding-bottom, $font-size--p--xs, $font-size--p--sm, $font-size--p--md, $font-size--p--lg, $font-size--p--xl);
	
}
ol {
	
	list-style: decimal;
	padding-top: 0;
	@include toggleSizes(padding-left, ($font-size--p--xs * 2), ($font-size--p--sm * 2), 0, 0, 0);
	@include toggleSizes(padding-bottom, $font-size--p--xs, $font-size--p--sm, $font-size--p--md, $font-size--p--lg, $font-size--p--xl);
	
}

dt {
	padding-bottom: 0;
}

p, li, td  {
	
	> kbd,
	> code {
	
		font-size: 		inherit;
		font-weight: 	normal;
		
		@include toggleDarkMode(color, $code-color, $code-color-dark);
		@include toggleDarkMode(background, $code-background, $code-background-dark);
		
		border-radius: 	5px;
		padding:		0 3px;
		
	}
	
}

sup {
	font-feature-settings:		$sup-font-feature-settings;
	text-decoration: none;
	margin-left: 2px;
	
	a {
		text-decoration: none;
		
		&:hover {
			text-decoration: $link-text-decoration-hover;
		}
		&:focus {
			text-decoration: $link-text-decoration-focus;
		}
	}
}

blockquote {
	@include toggleDarkMode(background, $blockquote-background, $blockquote-background-dark);
	padding: 20px;
	@include toggleSizes(margin, $padding--p--xs, $padding--p--sm, $padding--p--md, $padding--p--lg, $padding--p--xl);
	
	p {		
		&:first-child {
			padding-top: 0;
		}
		&:last-child {
			padding-bottom: 0;
		}
	}
	
	code, kbd {
		font-family: $font-family;
		@include toggleDarkMode(color, $code-color--blockquote, $code-color--blockquote-dark);	
	}
	
	ul {
		padding-left: $font-size--p--xs * 1.5;
	}
	ol {
		
		padding-left: $font-size--p--xs * 2.5;
		&:last-child {
			padding-bottom: 0;
		}
	}
	
	p ~ ol, 
	p ~ ul {
		margin-top: -1 * $font-size--p--xs;
		padding-bottom: 0.5 * $font-size--p--xs;
	}
	
}
	
strong, b,dt {
	font-weight: 	bold;
}

em, i {
	font-style: 	italic;
}

u {
	text-decoration: underline;
}

center {
	text-align: center;
}

h1 {
	font-weight: 		$font-weight--h1;
	font-style: 		$font-style-h1;
	text-align: 		$text-align--h1;
	text-decoration: 	$text-decoration--h1;
	text-transform: 	$text-transform--h1;
	
	@include toggleSizes(font-size, $font-size--h1--xs, $font-size--h1--sm, $font-size--h1--md, $font-size--h1--lg, $font-size--h1--xl);
	@include toggleSizes(line-height, $line-height--h1--xs, $line-height--h1--sm, $line-height--h1--md, $line-height--h1--lg, $line-height--h1--xl);
	@include toggleSizes(padding, $padding--h1--xs, $padding--h1--sm, $padding--h1--md, $padding--h1--lg, $padding--h1--xl);
	
	&.display {
		@include toggleSizes(font-size, $font-size--display--xs, $font-size--display--sm, $font-size--display--md, $font-size--display--lg, $font-size--display--xl);
		@include toggleSizes(line-height, $line-height--display--xs, $line-height--display--sm, $line-height--display--md, $line-height--display--lg, $line-height--display--xl);
		@include toggleSizes(padding, $padding--display--xs, $padding--display--sm, $padding--display--md, $padding--display--lg, $padding--display--xl);
	}
}

h2 {
	font-weight: 		$font-weight--h2;
	font-style: 		$font-style-h2;
	text-align: 		$text-align--h2;
	text-decoration: 	$text-decoration--h2;
	text-transform: 	$text-transform--h2;
	
	@include toggleSizes(font-size, $font-size--h2--xs, $font-size--h2--sm, $font-size--h2--md, $font-size--h2--lg, $font-size--h2--xl);
	@include toggleSizes(line-height, $line-height--h2--xs, $line-height--h2--sm, $line-height--h2--md, $line-height--h2--lg, $line-height--h2--xl);
	@include toggleSizes(padding, $padding--h2--xs, $padding--h2--sm, $padding--h2--md, $padding--h2--lg, $padding--h2--xl);
}

h3 {
	font-weight: 		$font-weight--h3;
	font-style: 		$font-style-h3;
	text-align: 		$text-align--h3;
	text-decoration: 	$text-decoration--h3;
	text-transform: 	$text-transform--h3;
	
	@include toggleSizes(font-size, $font-size--h3--xs, $font-size--h3--sm, $font-size--h3--md, $font-size--h3--lg, $font-size--h3--xl);
	@include toggleSizes(line-height, $line-height--h3--xs, $line-height--h3--sm, $line-height--h3--md, $line-height--h3--lg, $line-height--h3--xl);
	@include toggleSizes(padding, $padding--h3--xs, $padding--h3--sm, $padding--h3--md, $padding--h3--lg, $padding--h3--xl);
}

h4 {
	font-weight: 		$font-weight--h4;
	font-style: 		$font-style-h4;
	text-align: 		$text-align--h4;
	text-decoration: 	$text-decoration--h4;
	text-transform: 	$text-transform--h4;
	
	@include toggleSizes(font-size, $font-size--h4--xs, $font-size--h4--sm, $font-size--h4--md, $font-size--h4--lg, $font-size--h4--xl);
	@include toggleSizes(line-height, $line-height--h4--xs, $line-height--h4--sm, $line-height--h4--md, $line-height--h4--lg, $line-height--h4--xl);
	@include toggleSizes(padding, $padding--h4--xs, $padding--h4--sm, $padding--h4--md, $padding--h4--lg, $padding--h4--xl);
}

h5 {
	font-weight: 		$font-weight--h5;
	font-style: 		$font-style-h5;
	text-align: 		$text-align--h5;
	text-decoration: 	$text-decoration--h5;
	text-transform: 	$text-transform--h5;
	
	@include toggleSizes(font-size, $font-size--h5--xs, $font-size--h5--sm, $font-size--h5--md, $font-size--h5--lg, $font-size--h5--xl);
	@include toggleSizes(line-height, $line-height--h5--xs, $line-height--h5--sm, $line-height--h5--md, $line-height--h5--lg, $line-height--h5--xl);
	@include toggleSizes(padding, $padding--h5--xs, $padding--h5--sm, $padding--h5--md, $padding--h5--lg, $padding--h5--xl);
}

h2 + h3 {
	padding-top: 0 !important;
}
h3 + h4 {
	margin: 0;
	padding-top: 0 !important;
}

pre {
	display: block;
	position: relative;
	
	overflow-x: scroll;
	
	@include toggleDarkMode(color, $code-color, $code-color-dark);
	@include toggleDarkMode(background, $code-background, $code-background-dark);
	
	font-family: $font-family;
	
	padding: $font-size--p--xs;
	@include toggleSizes(margin, $padding--p--xs, $padding--p--sm, $padding--p--md, $padding--p--lg, $padding--p--xl);
	
	code {
		@include toggleSizes(font-size, $font-size--p--xs, $font-size--p--sm, $font-size--p--md, $font-size--p--lg, $font-size--p--xl);
		@include toggleSizes(line-height, $line-height--p--xs, $line-height--p--sm, $line-height--p--md, $line-height--p--lg, $line-height--p--xl);
	}
	
}

table {
	width: 		100%;
	border-collapse: collapse;
	
	@include toggleSizes(font-size, $font-size--p--xs, $font-size--p--sm, $font-size--p--md, $font-size--p--lg, $font-size--p--xl);
	@include toggleSizes(line-height, $line-height--p--xs, $line-height--p--sm, $line-height--p--md, $line-height--p--lg, $line-height--p--xl);
	@include toggleSizes(margin, $padding--p--xs, $padding--p--sm, $padding--p--md, $padding--p--lg, $padding--p--xl);
	
	tr {
		@include toggleDarkMode(border-bottom, 1px solid $gray-200, 1px solid $gray-800);

		&:last-child {
			border-bottom: none !important;
		}
	}
	
	th, td {
		padding: 5px 10px;
		vertical-align: top;
		
		@include toggleDarkMode(border-left, 1px solid $gray-200, 1px solid $gray-800);
		
		&:first-child {
			border-left: none;
		}
	}
	
	thead, 
	tfoot {
		@extend strong;
		text-align: left;
	}
	
	thead {
		@include toggleDarkMode(border-bottom, 2px solid $black, 2px solid $white);
	}
	
	tfoot {
		@include toggleDarkMode(border-top, 2px solid $black, 2px solid $white);
	}
	
}